<template>
  <div class="film-box" style="height:100%">
    <!-- <div class="content" style="height:100%"> -->
    <div class="content" :style="{ minHeight: curHeight+'px'}" >
<!--      nav-->
      <div class="nav-box">
        <div class="nav-title">
          <span :class="titleActive==1?'active':''" @click="clickTtile1" class="title-aa">
            <!-- 币币 -->
            {{$t('marketVue.bibi')}}
            </span>
          <span :class="titleActive==2?'active':''" @click="clickTtile2" class="title-aa">
            <!-- 合约 -->
            {{$t('marketVue.heyue')}}
            </span>
        </div>
        <!-- <img src="../../assets/image/search.png" alt="" style="width: 0.44rem;height: 0.44rem;"> -->
      </div>
<!--      二级nav-->
      <div class="nav2-box">
        <div class="item" @click="userNav(0)"><span :class="isNav==0?'active':''">
          <!-- 自 选 -->
          {{$t('marketVue.zixuan')}}
          </span></div>
        <div class="item" @click="userNav(1)"><span :class="isNav==1?'active':''">BNB</span></div>
        <div class="item" @click="userNav(2)"><span :class="isNav==2?'active':''">BTC</span></div>
        <div class="item" @click="userNav(3)"><span :class="isNav==3?'active':''">ALTS</span></div>
        <div class="item" @click="userNav(4)"><span :class="isNav==4?'active':''">FIAT</span></div>
      </div>
<!--      二级nav中的头部-->
      <div class="thead">
        <!--<span style="color: #089CED"> </span>-->
        <div>
          <!-- 交易对 / 成交量  -->
          {{$t('marketVue.jiaoyidui_volume')}}
          </div>
        <div>
          <!-- 最新价格 -->
          {{$t('marketVue.latest_price')}}
          </div>
        <div>
          <!-- 24H涨跌 -->
          {{$t('marketVue.up_down_24H')}}
        </div>
      </div>
<!--      列表内容 循环此区域-->
      <div v-for="(item, index) in list" :key="index">
        <div class="list-box" :class="index % 2 == 0?'odd':'even'" @click="toDetails(item.name)">
          <div class="list-item1" style="width:3rem">
            <div class="container">
              <!-- <span style="font-size: 0.26rem;color: #656873;margin-top: 1rem;">/ BUSD</span> -->
              <p style="color: white;margin-bottom: 0.17rem"><span style="color: #fff;opacity: 0.5;">{{item.name}} </span></p>
              <p style="font-size: 0.24rem;">
                <!-- 24H量 -->
                {{$t('marketVue.volume_24H')}}
                 {{item.market_value}}</p>
            </div>
          </div>
          <div class="list-item">
            <div class="container">
              <p style="margin-bottom: 0.17rem" class="red-font" v-if="item.scale < 0">{{item.current_price_usd}}</p>
              <p style="margin-bottom: 0.17rem" class="red-font red-font1" v-else>{{item.current_price_usd}}</p>
              <p style="font-size: 0.24rem;">￥{{item.market_value*7}}</p>
            </div>
          </div>
          <div class="list-item" v-if="item.scale > 0">
            <div class="right-btn">+{{item.scale}}%</div>
          </div>
          <div class="list-item" v-else>
            <div class="right-btn1">{{item.scale}}%</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { coin } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
export default {
  components: {},
  data () {
    return {
      titleActive: 1,
      curHeight: '',
      parameter: {
        market: 'coin',
        page: 1,
        showpage: 50
      },
      list: [],
      isNav: 0
    }
  },
  created () {
    this.beforeMount()
    // this.parameter.u_id = sessionStorage.getItem('u_id')
    var data = objKeySort(this.parameter)
    var token = sgin(data)
    this.parameter.token = token
    this.marketList(this.parameter)
  },
  methods: {
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    //  点击第一个标题
    clickTtile1 () {
      this.titleActive = 1
    },
    //  点击第2个标题
    clickTtile2 () {
      this.titleActive = 2
    },
    toDetails (name) {
      var names = name.splie('/')[0].toLowerCase()
      if (names === 'usdt') {
        names = 'btc'
      }
      if (names === 'xrp') {
        names = 'lbth'
      }
      this.$router.push({
        // path: '/transaction',
        path: '/market/marketDetails',
        query: {
          name: names
        }
      })
    },
    userNav (type) {
      this.isNav = type
    },
    marketList (parameter) {
      var that = this
      coin(parameter).then(res => {
        console.log('res市场', res)
        that.list = res.data
      }).catch((err) => console.log(err))
    }
  },
  mounted () {
    // var aa = { tel: 13714213050 }
    // login(aa).then(res => {
    //   console.log(res)
    // })
  }
}
</script>
